.contact-page {
  width: 100%;
  max-width: 100%;
  margin: 0;
  padding: 0;
}

.blog-page {
  width: 100%;
  max-width: 100%;
  margin: 0;
  padding: 0;
}

.home-page {
  width: 100%;
  max-width: 100%;
  margin: 0;
  padding: 0;
  font-family: 'Baloo Bhaina 2';
}

.hero1-section {
  background: linear-gradient(135deg, #f6f5f7 0%, #e8e6e9 100%);
  padding: 0 60px;
  text-align: center;
  display: flex !important;
  align-items: center;
  justify-content: center;
  height: auto;
  width: 100% !important;
  max-width: 100% !important;
  overflow: hidden;
  margin-top: 70px;
}

.shape {
  position: absolute;
  z-index: 0;
  opacity: 0.45; /* mờ nhẹ cho cảm giác background */
  pointer-events: none; /* không ảnh hưởng click */
}

/* Con mèo bay nhẹ */
.shape-cat {
  top: 15%;
  left: 3%;
  max-width: 80px;
  width: 100%;
  animation: float 6s ease-in-out infinite;
}

/* Cuộn len xoay + bay */
.shape-yarn {
  top: 30%;
  left: 3%;
  max-width: 120px;
  width: 100%;
  animation: float 8s ease-in-out infinite, rotate 20s linear infinite;
}

/* Animation float lên xuống */
@keyframes float {
  0% {
    transform: translateY(0px);
  }
  50% {
    transform: translateY(-15px);
  }
  100% {
    transform: translateY(0px);
  }
}

/* Animation xoay */
@keyframes rotate {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.hero1-row{
  flex: 1;                
  display: flex;          
  align-items: center;
  justify-content: center;
}

.hero1-content {
  max-width: 800px;
  margin: 0 auto;
}

.hero1-title {
  font-size: 60px;
  font-weight: 800;
  color: #640D5F;
  margin-bottom: 20px;
  line-height: 1.1;        
  opacity: 0;                 /* ban đầu ẩn */
  transform: translateX(-100px); /* đẩy lệch sang trái */
  /* animation: slideIn 1.5s ease forwards; */
}



.hero1-subtitle {
  font-size: 40px;
  font-weight: 600;
  color: #0D1164;
  margin-bottom: 30px;
  line-height: 1.3;
  opacity: 0;                 /* ban đầu ẩn */
  transform: translateX(-100px); /* đẩy lệch sang trái */
  /* animation: slideIn 1.5s ease forwards; */
}

.hero1-description {
  font-size: 1.2rem;
  color: #666;
  line-height: 1.6;
  margin-bottom: 40px;
  max-width: 600px;
  margin-left: auto;
  margin-right: auto;
  opacity: 0;                 /* ban đầu ẩn */
  transform: translateX(-100px); /* đẩy lệch sang trái */
  /* animation: slideIn 1.5s ease forwards; */
}

