.hero1-read-more-button {
    position: relative;
    z-index: 1;
    overflow: hidden;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    column-gap: 10px;
    border: none;
    outline: none;
    width: 200px;
    height: 60px;
    border-radius: 30px;
    text-decoration: none;
    font-size: 18px;
    font-weight: bold;
    color: white;
    background-color: #EA2264;
    cursor: pointer;
    padding: 0px 10px;
    margin-top: 20px;
}

.hero1-read-more-button::before {
    transition: 500ms all ease;
    content: '';
    position: absolute;
    left: 0;
    bottom: 0;
    z-index: -1;
    background-color: #640D5F;
    width: 100%;
    height: 0%;
}

.hero1-read-more-button:hover::before {
    height: 100%;
}

.hero1-right-img {
  width: 100% !important;
  height: auto;
  opacity: 0;                 /* ban đầu ẩn */
  transform: translateX(100px); /* đẩy lệch sang trái */
  /* animation: slideInRight 1.5s ease forwards; */
}

.hero1-right-row {
  position: relative;
}

.welcome-message-badge-container {
  position: absolute;
  top: 50%;
  right: -20px;
  transform: translateY(-50%);
  display: inline-block;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  animation-name: infiniteSpin;
  animation-duration: 15s;
  animation-iteration-count: infinite;
  animation-timing-function: linear;
}

@keyframes infiniteSpin {
  from {
    transform: rotate(0deg);
  }
  to {
      transform: rotate(360deg);
  }
}

.welcome-message-badge {
  width: 200px;
  height: 200px;
}

.welcome-message-logo {
  position: absolute;
  width: 100px;
  height: 100px;
}

.hero2-wrapper {
