.hero2-col {
  flex: 1 1 calc(25% - 20px);
  display: flex;          
  align-items: center;    
  justify-content: center;
  padding: 20px;
  text-align: center;
}

.hero2-img-wrapper {
  position: relative;
  display: inline-block;
}

.hero2-img {
    height: 246px;
    width: 264px;
    margin: 0 auto;
    position: relative;
    margin-top: 62px;
    margin-bottom: 35px;
}

.hero2-img:hover {
  transform: rotate(30deg) scale(1.1);
  transition: transform 0.3s ease;
}

.hero2-img img {
  width: 100%;
  height: 245px;
  object-fit: cover;
  border-radius: 16px;
  cursor: pointer;
}

.shape-foot {
  position: absolute;
  top: 30px;   /* đẩy lên trên */
  right: -10px; /* dịch sang phải */
  width: 60px;
  opacity: 0.7;
  pointer-events: none;
  transform: rotate(65deg);
}

.hero2-team {
  font-size: 20px;
  font-weight: 700;
  color: #0D1164;
  margin-top: 15px;
  cursor: pointer;
}

.hero2-team:hover {
  color:#640D5F;
}

.hero2-name {
  font-size: 16px;
  color: #666;
  cursor: pointer;
}

.hero3-section {
  padding: 40px 60px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #0D1164;
  overflow: hidden;
  position: relative;
}

.shape-hand {
  position: absolute;
  top: 5%;
  left: 20%;
  max-width: 80px;
  width: 100%;
  animation: pawBounce 2s ease-in-out infinite;
}

@keyframes pawBounce {
  0%, 100% {
    transform: translateY(0) rotate(0deg);
  }
  50% {
    transform: translateY(-15px) rotate(10deg); /* nhảy nhẹ + nghiêng */
  }
}

.contact-booking {
  flex: 1;
  background: #0D1164;
  border-radius: 20px;
  padding: 30px;
  color: white;
  min-width: 300px;
  max-width: 800px;
  text-align: center;
}

.contact-booking h2 {
  margin-bottom: 30px;
  height: auto !important;
  font-size: 40px;
  font-weight: 700;
  color:#fff;
}

.booking-form {
  display: flex;
  flex-direction: column;
  gap: 20px;
  
}

.form-row {
  display: flex;
  gap: 20px;
  flex-wrap: wrap;
}

.form-group {
  flex: 1;
  display: flex;
  flex-direction: column;
}

.form-group label {
  font-size: 14px;
  margin-bottom: 6px;
  font-weight: bold;
  color: #dcdcdc;
}

.form-group input,
.form-group select {
  padding: 12px;
  border-radius: 8px;
  border: none;
  background: #1a3b8b;
  color: white;
  font-size: 14px;
}

.contact-btn {
  align-items: center;
  margin-top: 20px;
}

.submit-btn {
  background-color: #EA2264;
  color: white;
  padding: 15px 30px;
  border-radius: 30px;
  border: none;
  cursor: pointer;
  font-size: 16px;
  font-weight: bold;
  overflow: hidden;
  align-self: flex-start;
}

.submit-btn::before {
    transition: 500ms all ease;
    content: '';
    position: absolute;
    left: 0;
    bottom: 0;
    z-index: -1;
    background-color: #640D5F;
    width: 100%;
    height: 0%;
    /* border-radius: 30px; */
}

.submit-btn:hover::before {
    height: 100%;
}



