.marquee__area {
    background:#0D1164;
    overflow: hidden;
    padding: 36px 0px 30px;
}

.marquee__wrap {
    display: flex;
    align-items: center;
}

.marquee__box {
    display: flex;
    align-items: center;
    flex-wrap: nowrap;
    width: fit-content;
    animation: 100s linear 0s infinite normal none running marquee;
}

.marquee__box:hover {
  animation-play-state: paused;
}

.marquee__box span {
    font-weight: 500;
    font-size: 24px;
    white-space: nowrap;
    line-height: 1;
    position: relative;
    margin-right: 54px;
    text-transform: uppercase;
    display: -webkit-box;
    display: -moz-box;
    display: -ms-flexbox;
    display: -webkit-flex;
    display: flex
;
    gap: 20px;
    align-items: center;
    color: #fff;
}

.emergency-section {
  padding: 80px 60px;
  background: #fff;
}

.emergency-row {
  display: flex;
  align-items: center;
  gap: 50px;
  flex-wrap: wrap;
}

.emergency-image {
  position: relative;
  flex: 1;
  text-align: center;
}

.emergency-image img {
  max-width: 100%;
  border-radius: 20px;
}

.shape {
  position: absolute;
  width: 80px;
  opacity: 0.8;
}

.shape.crown {
  left: 24%;
    top: -4%;
  width: 100px;
}

.shape.bowl {
  right: 10%;
    bottom: 20px;
  width: 90px;
}

.emergency-content {
  flex: 1;
}

.emergency-subtitle {
  font-size: 16px;
  font-weight: bold;
  color: #a64ca6;
  margin-bottom: 10px;
}

.emergency-title {
  font-size: 36px;
  font-weight: 800;
  color: #0D1164;
  margin-bottom: 50px;
  line-height: 1.3;
}

.emergency-desc {
  font-size: 16px;
  color: #555;
  margin-bottom: 30px;
  line-height: 1.6;
}

.emergency-features {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 20px 30px;
}

.feature {
  display: flex;
  align-items: flex-start;
  gap: 12px;
}

.feature .icon {
  display: inline-block;
  background: #f2e6f9;
  color: #640D5F;
  font-weight: bold;
  border-radius: 50%;
  padding: 8px 12px;
  font-size: 14px;
}

.feature h3 {
  font-size: 18px;
  font-weight: 700;
  color: #0D1164;
  margin-bottom: 5px;
}

.feature p {
  font-size: 14px;
  color: #555;
  line-height: 1.5;
}


@keyframes marquee {
  0%   { transform: translateX(0); }
  100% { transform: translateX(-100%); }
}


@media (max-width: 768px) {
  .hero1-section {
    flex-direction: column;
    margin-top: 100px;
  }
}